import { useState, useEffect } from 'react';
import { useWeb3 } from '../web3';
import { useAddresses } from '../web3/chains';
import {
  MultiRegister, MultiRegister__factory,
  IETHRegistrarController, IETHRegistrarController__factory,
} from '../../contracts/typechain';

const useMultiRegisterContract = () => {
  const { chainId, signerOrProvider } = useWeb3();
  const { multiRegister } = useAddresses(chainId);
  const [multiRegisterContract, setMultiRegisterContract] = useState<MultiRegister>();

  useEffect(() => {
    if (!chainId || !multiRegister || !signerOrProvider) {
      setMultiRegisterContract(undefined);
      return;
    }

    setMultiRegisterContract(MultiRegister__factory.connect(multiRegister, signerOrProvider));
  }, [chainId, multiRegister, signerOrProvider]);

  return multiRegisterContract;
}

const useRegistrarContract = (multiRegisterContract: MultiRegister | undefined) => {
  const { signerOrProvider } = useWeb3();
  const [registrarContract, setRegistrarContract] = useState<IETHRegistrarController>();

  useEffect(() => {
    if (!multiRegisterContract || !signerOrProvider) {
      setRegistrarContract(undefined);
      return;
    }

    multiRegisterContract.getRegistrar()
      .then(registrarAddress => setRegistrarContract(IETHRegistrarController__factory.connect(registrarAddress, signerOrProvider)))
      .catch(console.error);
  }, [multiRegisterContract, signerOrProvider]);

  return registrarContract;
}

export {
  useMultiRegisterContract,
  useRegistrarContract,
}
