import { useState, useEffect } from 'react';
import { BigNumber } from 'ethers';
import { useData } from './data';
import { useWeb3 } from './web3';
import { useTransaction } from './web3/transactions';
import Button from './Button';

function Register({
  waited,
  names,
  secrets,
  duration,
  totalCost,
  setReset,
}: {
  waited: boolean,
  names: string[],
  secrets: Uint8Array[],
  duration: number,
  totalCost: BigNumber,
  setReset: React.Dispatch<React.SetStateAction<boolean>>,
}) {
  const { contracts: { multiRegister } } = useData();
  const { account } = useWeb3();
  const { contractCall, pending } = useTransaction();
  const [buttonDisabled, setButtonDisabled] = useState(true);

  useEffect(() => {
    setButtonDisabled(pending || !waited);
  }, [waited, pending]);

  const register = () => {
    if (!account || !multiRegister) {
      return;
    }

    contractCall(
      () => multiRegister.multiRegister(account, duration, names, secrets, { value: totalCost }),
      "Registering names", "Registering names failed", "Registering names succeeded",
      undefined, () => setReset(true),
    );
  }

  return (
    <div className="mb-4">
      {secrets.length === names.length && (
        <Button
          disabled={buttonDisabled}
          onClick={register}
        >
          Register names
        </Button>
      )}
    </div>
  );
}

export default Register;
