import { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { useData } from './data';
import { useWeb3 } from './web3';
import { useTransaction } from './web3/transactions';
import Button from './Button';

function Commit({
  committed,
  setCommitted,
  setWaited,
  names,
  setSecrets,
  reset,
}: {
  committed: boolean,
  setCommitted: React.Dispatch<React.SetStateAction<boolean>>,
  setWaited: React.Dispatch<React.SetStateAction<boolean>>,
  names: string[],
  setSecrets: React.Dispatch<React.SetStateAction<Uint8Array[]>>,
  reset: boolean,
}) {
  const { contracts: { multiRegister } } = useData();
  const { account } = useWeb3();
  const { contractCall, pending } = useTransaction();
  const [commitments, setCommitments] = useState<string[]>([]);
  const [disableButton, setDisableButton] = useState(true);
  const [totalWait] = useState(70);
  const [countdown, setCountdown] = useState(totalWait);

  useEffect(() => {
    setDisableButton(pending || committed);
  }, [pending, committed]);

  useEffect(() => {
    if (!account) {
      setSecrets([]);
      setCommitments([]);
      return;
    }

    const labels = names.map(name => ethers.utils.keccak256(Buffer.from(name)));
    const secrets = names.map(_ => ethers.utils.randomBytes(32));
    setSecrets(secrets);
    const commitments = names.map((_, i) => ethers.utils.solidityKeccak256(["bytes32", "address", "bytes32"], [labels[i], account, secrets[i]]));
    setCommitments(commitments);
  }, [account, names, setSecrets]);

  useEffect(() => {
    if (reset) {
      setSecrets([]);
      setCommitted(false);
    }
  }, [reset, setCommitted, setSecrets]);

  const commit = () => {
    if (!multiRegister) {
      return;
    }

    contractCall(
      () => multiRegister.multiCommit(commitments),
      "Committing to names", "Committing to names failed", "Committing to names succeeded",
      undefined, () => setCommitted(true),
    );
  }

  useEffect(() => {
    if (!committed) {
      return;
    }

    const interval = setInterval(() => {
      setCountdown(countdown => {
        const newCountdown = countdown - 1;
        if (newCountdown === 0) {
          clearInterval(interval);
        }
        return newCountdown;
      });

    }, 1000);
  }, [committed]);

  useEffect(() => {
    if (countdown === 0) {
      setWaited(true);
    }
  }, [countdown, setWaited]);

  return (
    <div className="mb-4">
      {commitments.length === names.length && (
        <div>
          <Button
            disabled={disableButton}
            onClick={commit}
          >
            Commit to names
          </Button>
          {committed && (
            <div className="mt-4">
              <div className="italic text-3xl mb-2">Don't refresh or nagivate anywhere bozo</div>
              <div>Wait <span className="font-mono">{countdown}</span> seconds</div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default Commit;
