import { useEffect, useState } from 'react';
import { BigNumber } from 'ethers';
import Header from './Header';
import EnterNames from './EnterNames';
import Commit from './Commit';
import Register from './Register';
import { useWeb3 } from './web3';

function App() {
  const { account } = useWeb3();
  const [names, setNames] = useState<string[]>([]);
  const [secrets, setSecrets] = useState<Uint8Array[]>([]);
  const [duration, setDuration] = useState<number>(0);
  const [totalCost, setTotalCost] = useState(BigNumber.from(0))
  const [showActions, setShowActions] = useState(false);
  const [showRegister, setShowRegister] = useState(false);
  const [committed, setCommitted] = useState(false);
  const [waited, setWaited] = useState(false);
  const [reset, setReset] = useState(false);

  useEffect(() => {
    if (reset) {
      setReset(false);
    }
  }, [reset]);

  useEffect(() => {
    setShowActions(!!account && names.length > 0);
  }, [account, names.length]);

  useEffect(() => {
    setShowRegister(committed);
  }, [committed]);

  return (
    <div className="container">
      <Header />
      <EnterNames
        setNames={setNames}
        duration={duration}
        setDuration={setDuration}
        totalCost={totalCost}
        setTotalCost={setTotalCost}
        reset={reset}
      />
      {showActions && (
        <div>
          <Commit
            committed={committed}
            setCommitted={setCommitted}
            setWaited={setWaited}
            names={names}
            setSecrets={setSecrets}
            reset={reset}
          />
          {showRegister && (
            <Register
              waited={waited}
              names={names}
              secrets={secrets}
              duration={duration}
              totalCost={totalCost}
              setReset={setReset}
            />
          )}
        </div>
      )}
    </div>
  );
}

export default App;
