import { useState, useEffect, useCallback } from 'react';
import { BigNumber, ethers } from 'ethers';
import { normalize } from '@ensdomains/eth-ens-namehash';
import { validate } from '@ensdomains/ens-validation';
import { useData } from './data';
import { useWeb3 } from './web3';
import Button from './Button';

function GoodName({
  name,
  duration,
  addToTotalCost,
}: {
  name: string,
  duration: number,
  addToTotalCost: (name: string, price: BigNumber) => void,
}) {
  const { contracts: { registrar } } = useData();
  const [fullName] = useState(`${name}.eth`)
  const [price, setPrice] = useState<BigNumber>();

  useEffect(() => {
    if (!registrar) {
      setPrice(undefined);
      return;
    }

    registrar.rentPrice(name, duration)
      .then(rentPrice => setPrice(rentPrice.add(rentPrice.mul(10).div(100))))
      .catch(console.error);
  }, [name, registrar, duration]);

  useEffect(() => {
    if (!price) {
      return;
    }

    addToTotalCost(name, price);
  }, [addToTotalCost, name, price]);

  return (
    <li>{fullName} {price && <span className="text-sm">({ethers.utils.formatEther(price)}Ξ)</span>}</li>
  );
}

function TakenName({
  name,
}: {
  name: string,
}) {
  const { provider } = useWeb3();
  const [fullName] = useState(`${name}.eth`)
  const [resolvedAddress, setResolvedAddress] = useState("");
  const [reverseName, setReverseName] = useState("");

  useEffect(() => {
    if (!provider) {
      setResolvedAddress("");
      return;
    }

    provider.resolveName(fullName)
      .then(setResolvedAddress)
      .catch(console.error);
  }, [name, provider, fullName]);

  useEffect(() => {
    if (!provider || !resolvedAddress) {
      setReverseName("");
      return;
    }

    provider.lookupAddress(resolvedAddress)
      .then(setReverseName)
      .catch(console.error);
  }, [provider, resolvedAddress]);

  return (
    <li>
      <a
        className="mr-1"
        href={`https://app.ens.domains/name/${fullName}`}
        target="_blank"
        rel="noreferrer"
      >
        {fullName}
      </a>
      {resolvedAddress && (
        <span className="text-sm">(resolves to <a
          href={`https://app.ens.domains/address/${resolvedAddress}`}
          target="_blank"
          rel="noreferrer"
        >
          {resolvedAddress}
        </a>{reverseName && (
          <span className="ml-1">(<a
            href={`https://app.ens.domains/name/${reverseName}`}
            target="_blank"
            rel="noreferrer"
          >
            {reverseName}
          </a>)</span>
        )})</span>
      )}
    </li>
  );
}

function Validated({
  emoji,
  reason,
  result,
  children,
}: {
  emoji: string,
  reason: string,
  result: string,
  children: React.ReactNode,
}) {
  return (
    <div className="mb-4">
      <div className="text-2xl mb-2">
        {emoji} These names are <span className="font-bold">{reason}</span> and will be <span className="uppercase font-bold">{result}</span>
      </div>
      <ol className="list-decimal list-inside">
        {children}
      </ol>
    </div>
  );
}

function EnterNames({
  setNames,
  duration,
  setDuration,
  totalCost,
  setTotalCost,
  reset,
}: {
  setNames: React.Dispatch<React.SetStateAction<string[]>>,
  duration: number,
  setDuration: React.Dispatch<React.SetStateAction<number>>,
  totalCost: BigNumber,
  setTotalCost: React.Dispatch<React.SetStateAction<BigNumber>>,
  reset: boolean,
}) {
  const { contracts: { registrar } } = useData();

  const [maxNames] = useState(100);
  const [registrationYears, setRegistrationYears] = useState(1);
  const [newNames, setNewNames] = useState<string[]>([]);
  const [newNamesLength, setNewNamesLength] = useState(0);
  const [goodNames, setGoodNames] = useState<string[]>([]);
  const [shortNames, setShortNames] = useState<string[]>([]);
  const [takenNames, setTakenNames] = useState<string[]>([]);
  const [malformedNames, setMalformedNames] = useState<string[]>([]);
  const [validateNames, setValidateNames] = useState(false);
  const [validateDisabled, setValidateDisabled] = useState(true);
  const [prices, setPrices] = useState<{ name: string, price: BigNumber }[]>([]);

  useEffect(() => {
    const actualNames = newNames.filter(n => n.trim().length > 0);
    setNewNamesLength(actualNames.length)
  }, [newNames]);

  useEffect(() => {
    setValidateDisabled(newNamesLength === 0 || validateNames || newNamesLength > maxNames);
  }, [maxNames, newNamesLength, validateNames]);

  useEffect(() => {
    setNames(goodNames);
  }, [goodNames, setNames]);

  useEffect(() => {
    setDuration(60 * 60 * 24 * 365 * registrationYears);
  }, [registrationYears, setDuration]);

  useEffect(() => {
    if (!validateNames || !registrar) {
      setGoodNames([]);
      setShortNames([]);
      setMalformedNames([]);
      setTakenNames([]);
      setPrices([]);
      return;
    }

    const unique = [...new Set(newNames)];
    const actual = unique.filter(n => n.trim().length > 0);
    const short = actual.filter(n => n.length < 3);
    setShortNames(short);

    const notShort = actual.filter(n => n.length >= 3);
    const malformed = notShort.filter(n => !validate(n) || n.indexOf(".") > -1);
    const maybeNotMalformed = notShort.filter(n => validate(n) && n.indexOf(".") === -1);
    let maybeValid: string[] = [];
    maybeNotMalformed.forEach(n => {
      try {
        const normalized = normalize(n);
        maybeValid.push(normalized);
      } catch (e) {
        malformed.push(n);
      }
    });
    setMalformedNames(malformed);

    Promise.all(maybeValid.map(n => Promise.all([n, registrar.available(n)])))
      .then(availables => {
        setTakenNames(availables.filter(([, available]) => !available).map(([name]) => name));
        setGoodNames(availables.filter(([, available]) => available).map(([name]) => name));
      })
      .catch(console.error);
  }, [newNames, registrar, setTotalCost, validateNames]);

  useEffect(() => {
    setTotalCost(prices.reduce((p, n) => p.add(n.price), BigNumber.from(0)));
  }, [setTotalCost, prices]);

  useEffect(() => {
    if (reset) {
      setValidateNames(false);
    }
  }, [reset]);

  const addToTotalCost = useCallback((name: string, price: BigNumber) => {
    setPrices(prices => {
      const newPrices = [...prices];
      const existingPriceIndex = newPrices.findIndex(p => p.name === name);
      if (existingPriceIndex === -1) {
        newPrices.push({ name, price });
      } else {
        newPrices[existingPriceIndex].price = price;
      }
      return newPrices;
    });
  }, []);

  return (
    <div>
      <div className="mb-4">
        <div className="text-2xl mb-2">
          Register Multiple Names
        </div>

        <div className="flex justify-between">
          <div className="text-sm">
            One new ENS name per line, don't include <span className="font-mono">.eth</span>
          </div>
          <div className="text-sm">
            {newNamesLength}/{maxNames}
          </div>
        </div>

        <textarea
          className="w-full border p-2"
          rows={10}
          value={newNames.join(`\n`)}
          disabled={validateNames}
          onChange={(e) => setNewNames(e.target.value.split(`\n`).map(n => n.trim()))}
        />

        <div className="flex">
          <Button
            disabled={validateDisabled}
            onClick={() => setValidateNames(true)}
          >
            Validate names
          </Button>
          <div className="mx-1" />
          <Button
            disabled={!validateNames}
            onClick={() => setValidateNames(false)}
          >
            Edit names
          </Button>
        </div>
      </div>

      {shortNames.length > 0 && (
        <Validated emoji={`👎`} reason="too short" result="skipped">
          {shortNames.map((n, i) => <li key={i}>{n}</li>)}
        </Validated>
      )}

      {takenNames.length > 0 && (
        <Validated emoji={`👎`} reason="already taken" result="skipped">
          {takenNames.map((n, i) => (
            <TakenName
              key={i}
              name={n}
            />
          ))}
        </Validated>
      )}

      {malformedNames.length > 0 && (
        <Validated emoji={`👎`} reason="malformed" result="skipped">
          {malformedNames.map((n, i) => <li key={i}>{n}</li>)}
        </Validated>
      )}

      {goodNames.length > 0 && (
        <Validated emoji={`👍`} reason="looking good" result="registered">
          {goodNames.map((n, i) => (
            <GoodName
              key={i}
              name={n}
              duration={duration}
              addToTotalCost={addToTotalCost}
            />
          ))}
          <div className="my-2 flex items-center">
            <div>
              <Button
                disabled={registrationYears === 1}
                onClick={() => setRegistrationYears(years => years - 1)}
              >
                -
              </Button>
            </div>
            <div className="mx-2">
              {registrationYears} {registrationYears > 1 ? "years" : "year"}
            </div>
            <div>
              <Button
                disabled={false}
                onClick={() => setRegistrationYears(years => years + 1)}
              >
                +
              </Button>
            </div>
          </div>
          <div>
            <div className="text-xl font-bold">Total cost</div>
            <div>{ethers.utils.formatEther(totalCost)}Ξ</div>
          </div>
        </Validated>
      )}
    </div>
  );
}

export default EnterNames;
