import { useWeb3 } from './web3';
import { connect } from './web3/providers';
import Button from './Button';

function Header() {
  const { account } = useWeb3();

  return (
    <div className="py-4">
      <div className="text-4xl mb-2">
        ENSDAO Multi Register
      </div>
      <div>
        {!account && (
          <Button
            onClick={connect}
            disabled={false}
          >
            Connect wallet
          </Button>
        )}
        {account && (
          <div>Connected with <a
            className="mr-1"
            href={`https://app.ens.domains/address/${account}`}
            target="_blank"
            rel="noreferrer"
          >
            {account}
          </a></div>
        )}
      </div>
    </div>
  );
}

export default Header;
