import {
  MultiRegister,
  IETHRegistrarController,
} from '../../contracts/typechain';

import {
  useMultiRegisterContract,
  useRegistrarContract,
} from './contracts';

export interface Data {
  contracts: {
    multiRegister: MultiRegister | undefined,
    registrar: IETHRegistrarController | undefined,
  },
};

function useSystemData() {
  const multiRegisterContract = useMultiRegisterContract();
  const registrarContract = useRegistrarContract(multiRegisterContract);
  
  const data: Data = {
    contracts: {
      multiRegister: multiRegisterContract,
      registrar: registrarContract,
    },
  };

  return data;
}

export { useSystemData };
